@use "../../styles/mixin.scss" as mixin;
@use "../../styles/shared.scss" as color;

.slider {
   position: relative;
   display: flex;
   height: 100%;

   &__slider-image {
      object-fit: cover;
      width: 100%;
      height: 100%;
   }
}

.next,
.prev {
   top: calc(50% - 20px);
   position: absolute;
   background: color.$yellow;
   border-radius: 30px;
   width: 40px;
   height: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   user-select: none;
   cursor: pointer;
   font-weight: bold;
   font-size: 18px;
   z-index: 2;
}

.next {
   right: 10px;
}

.prev {
   left: 10px;
   transform: scale(-1);
}